*, *:before, *:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

html {
  font-size: 10px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

body {
  color: #3c262b;
  font-size: 3rem;
  font-family: "VT323", Arial, Helvetica, sans-serif;
}

p {
  margin: 2rem 0;
}

h1 {
  margin: 0;
}

svg {
  width: inherit;
  filter: drop-shadow(1.5px 1.5px 1.5px #000);
}

.b-main {
  background-color: #0261bb;
  background-image: linear-gradient(#023b7e, #0261bb, #259bfd, #5edceb, #93fedb);
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  padding: 5% 8%;
}
.b-main a {
  color: #853605;
}
.b-main a:hover {
  color: #cf5408;
}
.b-main > * {
  margin: 3rem 0;
}
.b-main__title {
  font-family: "Londrina Solid", Arial, Helvetica, sans-serif;
  font-size: 8rem;
  color: #e7b012;
  text-shadow: #000 3px 3px 3px;
  letter-spacing: 1rem;
  text-align: center;
  text-transform: uppercase;
}

.b-text {
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.1));
  backdrop-filter: blur(2rem);
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.5) 3px 3px 3px;
  padding: 1rem 4rem;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}
.b-text__icon--id_bus {
  height: 2rem;
  filter: drop-shadow(1.5px 1.5px 1.5px #000);
}
.b-text__icon--id_junimo {
  width: 4rem;
  margin: 1rem 2rem;
  filter: drop-shadow(1.5px 1.5px 1.5px rgba(0, 0, 0, 0.5));
}

.b-acknow__links-ul {
  padding-left: 0;
}
.b-acknow__links-ul--nested {
  margin-left: 4rem;
}
.b-acknow__links-li {
  list-style: none;
  margin: 1.5rem 0;
}
.b-acknow__links-li--id_nativeland {
  background-image: url("../assets/png/misc/bundle_green.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}
.b-acknow__links-li--id_wapikoni {
  background-image: url("../assets/png/misc/bundle_yellow.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}
.b-acknow__links-li--id_support > span {
  background-image: url("../assets/png/misc/bundle_orange.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}
.b-acknow__links-li--nested {
  background-image: url("../assets/png/misc/blackberry.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
  background-size: 2.2rem;
  padding-top: 0.5rem;
}

.b-map {
  height: 90vh;
  width: 100%;
  border: 10px solid #923e08;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.5) 3px 3px 3px;
}

.clock {
  background-color: #ffd787;
  font-family: "VT323", Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
  border: #f7ba00 solid 10px;
}

.b-credits {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.b-credits__icon {
  height: max-content;
  filter: drop-shadow(1.5px 1.5px 1.5px rgba(0, 0, 0, 0.5));
}
.b-credits__text {
  margin: 0 0 0 2rem;
}

.b-footer {
  color: #dde0ee;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  letter-spacing: 0.05rem;
  word-spacing: 0.2rem;
  background-color: #09543b;
  font-size: 2.5rem;
  text-align: center;
  padding: 1.5rem 0;
}
.b-footer a {
  color: #fdbd6f;
}
.b-footer a:hover {
  color: #fc9a24;
}
.b-footer > * {
  margin: 1.5rem 0;
}

.b-tagline__icon {
  width: 3rem;
  vertical-align: middle;
}

.b-stack__icon {
  margin: 0.2rem;
  vertical-align: middle;
  width: 3rem;
}
.b-stack__icon-link {
  width: inherit;
}
.b-stack__icon--id_sass {
  width: 3.3rem;
}
.b-stack__icon--id_js {
  width: 2.61rem;
}
.b-stack__icon--id_gmaps {
  width: 2.55rem;
}

.b-map {
  height: 90vh;
  width: 100%;
  border: 10px solid #923e08;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.5) 3px 3px 3px;
}

.clock {
  background-color: #ffd787;
  font-family: "VT323", Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
  border: #f7ba00 solid 10px;
  margin: 15px;
}
.clock-icons {
  height: min-content;
}
.clock-icons-season {
  width: 42px;
  border: #dc7b05 solid 3px;
}